.inputSection {
	display: flex;
	align-items: center;
	margin-bottom: 40px;

	p {
		font-size: 12px;
		line-height: 20px;
		color: #999999;
		margin: 0 0 0 18px;
	}
}

.inputArea p {
	margin-top: 4px;
	margin-bottom: 0;
	padding: 0;
	font-size: 12px;
	color: #ff0000;
	line-height: 20px;
}

.inputBox {
	box-sizing: border-box;
	width: 335px;
	height: 31px;
	border: 1px solid #999999;

	font-size: 16px;
	line-height: 19px;

	&:focus {
		outline: none;
	}
}

.redBorder {
	box-sizing: border-box;
	width: 335px;
	height: 31px;
	border: 1px solid red;

	font-size: 16px;
	line-height: 19px;

	&:focus {
		outline: none;
	}
}

.nonImportant {
	width: 224px;
}

.checkbox {
	font-size: 16px;
	line-height: 19px;
}

@import '../../../mediascreen';